ul
{
    list-style-type: none;
    position: fixed;
    top: 0px;
    width: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #142444;
}
  

li a
{
    float: left;
    display: block;
    color: #7b8384;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}
  
li a:hover
{
    color: #ebf3f4;
    background-color: #243454;
}
  
.right-end
{
    float: right;
    margin: 14px 16px;
}

li form
{
    float: right;
    margin: 12px 16px;
}

.search-bar
{
    margin-right: 5px;
    border: 2px;
    outline: 0;
    padding: 2px 4px;
    background: transparent;
    border-bottom: 1px solid #cbd3d4;
    color: #7b8384;
}

.search-bar:hover
{
    color: #ebf3f4;
    background-color: #243454;
}

.search-btn
{
    border: 0;
    background: transparent;
    color: #7b8384;
    padding: 4px 6px 4px 6px;
}

.search-btn:hover
{
    color: #ebf3f4;
    background-color: #243454;
}