.card-div
{
    width: 30%;
    height: fit-content;
    margin: 10px;
    text-align: center;
    border-color: transparent;
    border-style: double;
    border-width: 2px;
    border-radius: 10px;
    color: #ebf3f4;
    display: flex;
    flex-direction: column;
}

.card-div:hover
{
    background-color: #8b9394;
    border-color: #ebf3f4;
    border-style: double;
    border-width: 2px;
    border-radius: 10px;
}

.learn-more
{
    border: none;
    padding: 10px;
    font-size: 16px;
    background-color:  #344464 ;
    color: #ebf3f4;
}

.learn-more:hover
{
    background-color: #243454;
}

.card-details
{
    width: 80%;
    height: fit-content;
    align-self: center;
    font-size: 75%;
}

.card-part-1
{
    margin: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.card-part-2
{
    margin: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}