.footer-div
{
    display: flex;
    flex-direction: column;
    background-color: #142444;
    color: white;
    text-align: center;
    padding-bottom: 20px;
}

.socials-div
{
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.socials-div > a
{
    color: #8b9394;
    margin: 5px;
    padding: 10px;
}

.socials-div > a:hover
{
    color: #ebf3f4;
    background-color: #243454;
}

.copyright
{
    margin: 10px;
    padding: 10px;
}